/* stylelint-disable linebreaks */
.Button {
  background: rgb(12, 106, 248);
  border: 0;
  border-radius: 0.3em;
  color: rgba(255, 255, 255);
  opacity: 1;
  padding: 0.7em;
  padding-left: 3em;
  padding-right: 3em;
  transition: 250ms;
}

.Button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.div-Container {
  align-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: center;
}
