/* stylelint-disable linebreaks */
.minigameContainer {
  background-color: rgb(215, 215, 255);
  border: 0.25em solid black;
  border-radius: 0.3em;
  margin: 1em;
  width: 20em;
}

.button {
  background: rgb(12, 106, 248);
  border: 0 solid black;
  color: rgba(255, 255, 255);
  opacity: 1;
  padding: 0.5em;
  text-align: center;
  transition: 250ms;
  width: 100%;
}

.button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.Make {
  color: rgb(12, 106, 248);
  padding: 2em;
}

.won {
  background: #ffbc49;
  width: 100%;
}
